import { React, useState, useContext, useEffect } from 'react';
import { useCookies } from "react-cookie";
import { useNavigate } from "react-router-dom";
import { UserContext } from '../services/UserContext';
import { InfoContext } from '../services/InfoContext';

import fbLogo from './loginIcon/icons8-facebook.svg';

import { changeSocial } from '../services/api_methods';

const AddFacebook = () => {
  const [cookies, setCookie] = useCookies();
  const [facebook, setFacebook] = useState('');

  const { user, setUser } = useContext(UserContext);
  const { info, setInfo } = useContext(InfoContext);
  const navigate = useNavigate();

  // useEffect(() => {
  //     changeSocial({
  //         csrftoken: cookies.csrftoken,
  //         socialContact: 'socialContact'})
  //     .then(mes => console.log(mes))
  //   return () => {
  //   }
  // }, [])

  const handleChangeSocial = (e) => {
    e.preventDefault()
    console.log('COOKIES:',cookies)
    changeSocial({
      csrftoken: getCookie('csrftoken'),
      link: facebook,
      token: user.token
    })
      .then(mes => {
        setInfo({
          text: mes.message,
          type: 'success'
        })
        console.log(mes)
        navigate('/')
      })
      .catch(err => {
        err.then(res => console.log(res))
        setInfo({
        text: 'Nieprawidowy link.',
        type: 'error'
      })})
      
    }
    function getCookie(name) {
      let cookieValue = null;
      if (document.cookie && document.cookie !== '') {
          const cookies = document.cookie.split(';');
          for (let i = 0; i < cookies.length; i++) {
              const cookie = cookies[i].trim();
              // Does this cookie string begin with the name we want?
              if (cookie.substring(0, name.length + 1) === (name + '=')) {
                  cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                  break;
              }
          }
      }
      return cookieValue;
  }

return (
  <div className="Add-facebook-view">
    <span> Do prawidowego funkcjonowania aplikacji potrzebujemy linku do Twojego profilu na Facebooku. Bdzie on wywietlany tylko osobom ktre zdecydujesz si pozna. </span>
    <form>
      <div className={`input-form-line`}>
        <img className='fbLogo' src={fbLogo} alt="" />
        <input type="text" placeholder='Link do Twojego FB' onChange={(e) => setFacebook(e.target.value)}></input>
        
      </div>
      {/* <div className={`input-form-line`}>
        <img className='fbLogo' src={fbLogo} alt="" /> //jaka inna ikona
        <textarea type="text" placeholder='Powiedz nam co o sobie!' onChange={(e) => setDescription(e.target.value)}></textarea>
      </div> */}
      {/* <h4>Podaj nam kontakt do siebie!</h4>
      <input type="text" placeholder='link do Twojego profilu na Facebooku' onChange={
        (e) => setFacebook(e.target.value)
      }></input> */}
      <div className="center-content">
        <button className="action-button change" onClick={(e) => handleChangeSocial(e)}>{user?.socialContact ? 'Zmie' : 'Zatwierd'}</button>
        <button className="action-button" onClick={() => navigate('/')}>Menu</button>
      </div>
    </form>
    <button onClick={() => console.log(getCookie('csrftoken'))}>cookie</button>
  </div>
)
}

export default AddFacebook